export default {
	data() {
		return {
		headlineList:[],
		ListLen:0,
		}
	},
	onLoad:function(){
		
		var _self = this
		_self.getInfo()
	},
	methods: {
		getInfo:function(){
			var _self = this
			uni.request({
				url:_self.$api+"dockingManager/releaseMainQuery",
				data:{id:"0",pull:2,optionId:uni.getStorageSync("openId")}, 
				method:"GET",
				success:function(res){
					console.log(res)
					_self.headlineList = res.data
					_self.ListLen = _self.headlineList.length
				}
			}) 
		},
		goto:function(id){
			uni.navigateTo({
				url:"../headerDetail/headerDetail?id="+id
			})
		},
	}
}
